import React, { useState, useEffect } from "react";
import { dbService } from "../fbase";
import logo from "../logo/logo7.png"
import styles from "../styleModule/confirm.module.css"
import { useHistory } from 'react-router-dom';



const Confirm = ({match}) => {
    let history = useHistory();
    const clientName = match.params.client;
    const session = match.params.session;
    
    const [confirmDate, setConfirmDate] = useState(null);
    const [noteConfirm, setNoteComfirm] = useState("Initializing");
    const getProgressData = async () => {
        const progresses = await dbService.collection('sessions').where("회원", "==", clientName).where("회차", '==', session).get()
        const progressesArray = progresses.docs.map(progress => progress.data());
        console.log(progressesArray);
        setConfirmDate(progressesArray[0].진행날짜)
        setNoteComfirm(progressesArray[0].메모)
        //setProgressData(progressesArray)
    }
    const onClick = () => {
        history.push(`/clientProgress/${clientName}`);
    }

    useEffect(()=> {
        
        getProgressData()
        
    }, [])
    
    return (
        <div className={styles.firstdiv}>
            <div className={styles.logoFirstDiv}>
                <div className={styles.logoSecondDiv}>
                    <img alt="" src={logo} width="150px"/>    
                </div>
            </div>
            <div className={styles.secondDiv}>
                <span className={styles.confirmDataSpan}>
                    <span className={styles.firstConfirmData}>진행 날짜</span>
                    <span className={styles.secondConfirmData}>{confirmDate}</span>
                </span>
                <div id="1" className={styles.firstIdDiv}>
                    <textarea value={noteConfirm} 
                                rows="15" 
                                cols="28" 
                                readOnly
                                className={styles.textArea}/>
                    <div className={styles.firstIdBtnDiv}>
                            <span  onClick={onClick} className={styles.btn}>
                                확 인
                            </span>
                    </div>
                </div>
{/*             <div id="2" className={styles.secondIdDiv}>
                <img alt ="" src={confirmSign} className={styles.imgStyle}/>
            </div>
            <div className={styles.confirmBtnContainer}>
                <span className={styles.confirmBtn} onClick={onClick}> 
                    확 인 
                </span>
            </div>
            </div> */}
        </div>
        </div>
    )
}

export default Confirm;