import React, { useState, useEffect } from "react";
import logo from "../logo/logo7.png";
import styles from "../styleModule/note.module.css"
import { authService, dbService } from "../fbase";
import { Link } from "react-router-dom";

const Note = ({match}) => {
    const clientName = match.params.client;
    const session = match.params.session;
    const [unitPrice, setUnitPrice] = useState(null);
    const [profit, setProfit] = useState(0);
    const [remainSession, setRemainSession] = useState(null);
    const [note, setNote] = useState("");
    const [user, setUser] = useState({});
    const [classify, setClassify] = useState("");
    const [trainer, setTrainer] = useState(null);
    const [userProfile, setUserProfile] = useState(null);
    const dbData = dbService.collection('clients').doc(clientName);
    const onChange = (event) => {
        const {target: {value}} = event;
        setNote(value);
    }
    const asyncData = async () => {
        await dbData.get().then(doc => {
            setUnitPrice(doc.get("회당금액"))
            setRemainSession(doc.get("잔여횟수"))
            setClassify(doc.get("등록구분"))
            setTrainer(doc.get("트레이너"))
        });
    }

    const onClick = async () => {
        await dbService.collection("sessions").add({
            진행날짜: new Date().toLocaleString(),
            회차: session,
            회원: clientName,
            트레이너: user.uid,
            메모: note,
            회당금액: unitPrice
        })
        await dbService.doc(`clients/${clientName}`).set({
            진행회차: +session +1,
            잔여횟수: +remainSession -1
        }, {merge: true})

        // 붙여넣기 시작
        
        if (classify === "신규") {
            await dbService.doc(`trainers/${user.uid}/21.2/급여`).set({
                신수업일수: userProfile.신수업일수+1,
                신수업료: userProfile.신수업료+unitPrice*0.4,
                순이익: userProfile.순이익+unitPrice*0.4,
            }, {merge: true})
            await dbService.doc(`trainers/Z5rA2alLr1PChf7cMtK8Eva2JuS2/21.2/관리`).set({
                전체순이익: profit.전체순이익 + unitPrice*0.4
            }, {merge: true})
        } else if (classify === "재등록") {
            await dbService.doc(`trainers/${user.uid}/21.2/급여`).set({
                재수업일수: userProfile.재수업일수+1,
                재수업료: userProfile.재수업료+unitPrice*0.5,
                순이익: userProfile.순이익+unitPrice*0.5,
            }, {merge: true})
            await dbService.doc(`trainers/Z5rA2alLr1PChf7cMtK8Eva2JuS2/21.2/관리`).set({
                전체순이익: profit.전체순이익 + unitPrice*0.5
            }, {merge: true})
        } else if (classify === "신규소개") {
            await dbService.doc(`trainers/${user.uid}/21.2/급여`).set({
                소수업일수: userProfile.소수업일수+1,
                소수업료: userProfile.소수업료+unitPrice*0.6,
                순이익: userProfile.순이익+unitPrice*0.6,
            }, {merge: true})
            await dbService.doc(`trainers/Z5rA2alLr1PChf7cMtK8Eva2JuS2/21.2/관리`).set({
                전체순이익: profit.전체순이익 + unitPrice*0.6
            }, {merge: true})
        } else if (classify === "재등록소개") {
            await dbService.doc(`trainers/${user.uid}/21.2/급여`).set({
                소수업일수: userProfile.소수업일수+1,
                소수업료: userProfile.소수업료+unitPrice*0.6,
                순이익: userProfile.순이익+unitPrice*0.6,
            }, {merge: true})
            await dbService.doc(`trainers/Z5rA2alLr1PChf7cMtK8Eva2JuS2/21.2/관리`).set({
                전체순이익: profit.전체순이익 + unitPrice*0.6
            }, {merge: true})
        }
        
        
            // 트레이너 app에서 유저 객체로 전달...
    }
    useEffect(()=> {
        setUser(authService.currentUser)
        asyncData()
        dbService.collection("trainers").doc("Z5rA2alLr1PChf7cMtK8Eva2JuS2").collection("21.2").doc("관리").get().then(doc => setProfit(doc.data()))
    },[])
  
    useEffect(() => {
        if(trainer) {
             dbService.collection('trainers').doc(trainer).collection("21.2").doc("급여").get().then(doc => {
                
               
                
                
                setUserProfile(doc.data())
            }) 
            
        }
    }, [trainer])
    
    useEffect(() => {
        if(userProfile) {
            console.log(userProfile);
        }
    }, [userProfile])
    return (
        <div className={styles.firstDiv}>
            <div className={styles.logoFirstDiv}>
                <div className={styles.logoSecondDiv}>
                    <img alt="" src={logo} width="150px"/>    
                </div>
            </div>
            <div className={styles.textDiv}>
                <textarea  value={note} 
                                rows="15"
                                cols="28" 
                                placeholder="PT 메모" 
                                onChange={onChange}
                                className={styles.textArea}
                    />
            </div>
             <div className={styles.btnDiv}>
                <Link to={{pathname:`/signature/${clientName}/${session}`}} >
                    <button onClick={onClick} className={styles.btn}>
                            저 장
                    </button>
                </Link>
           </div>
            
        </div>
    )
}

export default Note;