import React, { useState, useEffect } from 'react'
import {useHistory} from 'react-router-dom';
import SignaturePad from 'react-signature-canvas'
import logo from "../logo/logo7.png"
import styles from '../styleModule/signature.module.css'
import { dbService } from '../fbase';



const Signature = ({match, location}) => {
    let history = useHistory();
    
    const [sessionId, setSessionId] = useState(null);
    const [trimmedDataURL, setTrimmedDataURL] = useState(null);
    const clientName = match.params.client;
    const session = match.params.session;
    
    
    useEffect(() => {
        getSessionData()
    }, [])


    const onSubmit = async () => {
        await dbService.collection('signatures').add({
            사인: trimmedDataURL,
            회원: clientName,
            회차: session
        })
        history.push(`/clientProgress/${clientName}`);
    }

    const getSessionData = async () => {
        const sessionData = await dbService.collection('sessions').where('회원', '==', clientName).where("회차", '==', session).get()
        sessionData.forEach(data => setSessionId(data.id))
    }

    let sigPad = {}
    const trim = () => {
        const DataUrl = sigPad.getTrimmedCanvas().toDataURL('image/png');
        setTrimmedDataURL(DataUrl);
        }
    useEffect(() => {
        console.log(sessionId);
    }, [sessionId])

    return (
        <div className={styles.container} >
            <div className={styles.logoFirstDiv}>
                <div className={styles.logoSecondDiv}>
                    <img alt="" src={logo} width="150px"/>    
                </div>
            </div>
            <div className={styles.secondDiv}>
                <div className={styles.sigContainer}>
                    <SignaturePad canvasProps={{className: styles.sigPad}}
                            ref={(ref) => { sigPad = ref }} />
                </div>
                <div className={styles.buttonContainer}>
                    <button className={styles.buttons} onClickCapture={trim}>
                            완료
                    </button>
                    <button className={styles.buttons} onClick={onSubmit}>
                            제출
                    </button>
                </div>
            </div>
        </div>
    )}


export default Signature;
