import React, { useEffect, useState } from 'react';
import { Link, useHistory } from "react-router-dom";
import { dbService } from '../fbase';
import styles from "../styleModule/clientProgress.module.css";
import logo from "../logo/logo7.png"

const ClientProgress = ({match}) => {
const history = useHistory();
 const [progressCount, setProgressCount] = useState(null);
 const [progressData, setProgressData] = useState([]);
 const clientName = match.params.client;

 const getClient = async () => {
     const client = await dbService.doc(`clients/${clientName}`).get()
     const clientData = client.data();
     setProgressCount(clientData.진행회차)
     if (clientData.잔여횟수 === 0) {
        await dbService.doc(`clients/${clientName}`).set({
            종결여부: "종료"
        }, {merge: true});
        await dbService.doc(`locker/${clientData.락커번호}`).set({
            회원: ""
        }, {merge: true})
     }
  }
 const getProgressData = async () => {
     const progresses = await dbService.collection('sessions').where("회원", "==", clientName).orderBy("회차").get()
     const progressesArray = progresses.docs.map(progress => progress.data());
     setProgressData(progressesArray)
 }

 const handleClick = () => {
     history.push(`/note/${clientName}/${progressCount}`)
 }
 useEffect(() => {
    getClient()
    getProgressData()
 }, [])
 //console.log(progressData);
 return (
    <div className={styles.firstDiv}>
        <div className={styles.secondDiv}>
                <div style={{
                    width:"100%",
                    display:"flex",
                    justifyContent:"center",
                    alignItem:"center",
                }}>
                    <img alt="" src={logo} width="150px"/>  
                </div>
        </div>
         
         <div className={styles.listContainer}>
             <div className={styles.childListContainer}>
             {progressData.map(data => 
                <div key={data.회차} className={styles.FirstChild}>
                    <span> {data.회차}  회차</span>
                    <span className={styles.btn}>
                    <Link to={`/confirm/${clientName}/${data.회차}`} >
                        <span style={{textDecoration: "none", color: 'white', fontWeight: "500"}}>확 인</span>
                    </Link>
                    </span>
                </div>
                )}
                </div>
         </div>
         <div className={styles.btnDiv}>
        
          <div className={styles.onProgress} onClick={handleClick}>진 행</div>
       
         </div>
     </div>
 )
}

export default ClientProgress