import React from "react";
import { HashRouter as Router, Route, Switch, Redirect} from "react-router-dom";
import Home from "../routes/Home";
import Profile from "../routes/Profile";

import AddClient from "../routes/AddClient";
import ClientDetail from "../routes/ClientDetail";
import Auth from "../routes/Auth";
import Navigation from "./Navigation";

import ClientProgress from "../routes/ClientProgress";
import Signature from "../routes/Signature";
import Confirm from "../routes/Confirm";
import Note from "../routes/Note";


const AppRouter = ({isLoggedIn, userObj}) => {
    return (
        <Router>
            <Switch>
                {isLoggedIn ? (
                    <>
                        <Route exact path="/">
                            <Home userObj={userObj}/>
                        </Route>
                        <Route exact path="/profile">
                            <Profile userObj={userObj}/>
                        </Route>
                        <Route exact path="/addClient">
                            <AddClient userObj={userObj}/>
                        </Route>
                        <Route  path="/clientDetail/:client" component={ClientDetail}>
                        </Route>
                        <Route  path="/clientProgress/:client" component={ClientProgress}>
                        </Route>
                        <Route  path="/signature/:client/:session" component={Signature}>
                        </Route>
                        <Route  path="/confirm/:client/:session" component={Confirm}>
                        </Route>
                        <Route path="/note/:client/:session" component={Note}>
                        </Route>
                    </>
                        ) : (
                            <>
                                <Route exact path="/">
                                    <Auth />
                                </Route>
                                <Redirect from="*" to="/" />
                            </>
                        )}
            </Switch>
            {isLoggedIn && <Navigation/>}
        </Router>
    )
}

export default AppRouter;