import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { dbService } from "../fbase";
import styles from "../styleModule/home.module.css";
import logo from "../logo/logo7.png";

const Home = ({userObj}) => {
    const [clients, setClients] = useState([]);
    const getMyClient = async () => {
        const dbClients = await dbService.collection("clients").where("트레이너", "==", userObj.uid).where("종결여부", "==", "진행중").orderBy("잔여횟수").get()
        const myClientsArray = dbClients.docs.map(client => client.data());
        setClients(myClientsArray)
    }

    useEffect(()=> {
        //월말 초기화 로직
    /*     console.log(userObj.uid);
    dbService.doc(`trainers/${userObj.uid}/21.2/급여`).set({
        소수업일수: 0,
        소수업료: 0,
        신수업일수:0,
        신수업료:0,
        재수업일수:0,
        재수업료:0,
        순이익: 0,
        총매출: 0
    }, {merge: true}) */

    getMyClient()
    },[])
    return (
        <div className={styles.firstDiv}>
            <div className={styles.logoFirstDiv}>
                <div className={styles.logoSecondDiv}>
                    <img alt="" src={logo} width="150px"/>    
                </div>
            </div>
            <div className={styles.containerDiv}>
                <div className={styles.childContainer}>
            {clients.map(client =>
                <div key={client.이름+""+client.연락처} className={styles.clientContainer}>
                    <div className={styles.clientSubContainer}>
                        <Link to={`clientDetail/${client.이름}+${client.연락처}`} className={styles.clientDetailLink}>
                        <span> {client.이름} </span>
                        <span style={{
                            fontSize:"13px"
                        }}>( 잔여: {client.잔여횟수} )</span>
                        </Link>
                        <Link to={`clientProgress/${client.이름}+${client.연락처}`} className={styles.clientProgressLink}>
                            진 행
                        </Link>
                    </div>
                </div> )}
                </div>
            </div>
        </div>
    )
}
export default Home;