import React, { useState, useEffect } from "react";
import { dbService } from "../fbase";
import logo from "../logo/logo7.png";
import styles from "../styleModule/profile.module.css";
//로그인에서 유저 id parmas로 전달해서 구분
//지금은 상욱쌤 테스트 기간용 구분x
const Profile = ({userObj}) => {
    

  
    const [profileData, setProfileData] = useState(null);
    const [pay, setPay] = useState(0);
    

    
    const resetMeasure = () => {
        let measure = profileData.총매출-profileData.순이익;
        let registerP = profileData.재수업료;
        if (measure <= 2000000 ) {
            let adjust = registerP*0.8;
            let total = profileData.신수업료+profileData.소수업료+adjust;
            setPay(total);
        } else if (measure >= 2000000) {
            let adjust = registerP*1.2;
            let total = profileData.신수업료+profileData.소수업료+adjust;
            setPay(total);
        } else {
            let total = profileData.신수업료+profileData.소수업료+registerP;
            setPay(total);
        }    
    };
    
    useEffect(()=>{
        dbService.collection("trainers").doc(userObj.uid).collection("21.2").doc("급여").get().then(doc => setProfileData(doc.data()))
      
    },[])



    useEffect(() => {
        if (profileData) {
            resetMeasure()
        }
    }, [profileData])


    return (
        <div className={styles.firstDiv}> 
            
            <div className={styles.logoFirstDiv}>
            <div className={styles.logoSecondDiv}>
                <img alt="" src={logo} width="150px"/>    
            </div>
            </div>
            <span className={styles.containerDiv}>
            <div className={styles.dataContainer}>
                <span>수업일수(신): {profileData ? profileData.신수업일수 : "Initializing..."}</span>
                <span>수업일수(재): {profileData ? profileData.재수업일수 : "Initializing..."}</span>
                <span>수업일수(소): {profileData ? profileData.소수업일수 : "Initializing..."}</span>
            </div>
            <div className={styles.dataContainer}>
                <span>수업료(신): {profileData ? profileData.신수업료 : "Initializing..."}</span>
                <span>수업료(재): {profileData ? profileData.재수업료 : "Initializing..."}</span>
                <span>수업료(소): {profileData ? profileData.소수업료 : "Initializing..."}</span>
            </div>
            <div className={styles.dataContainer}>
                <span>총매출: {profileData ? profileData.총매출 : "Initializing..."}</span>
                <span>순수익: {profileData ? profileData.순이익 : "Initializing..."}</span>
                <span>급여(3.3%포함): {pay}</span>
                <span>급여(3.3%제외): {parseInt(pay*0.967)}</span>
            </div>
            </span>
            
        </div>
    )
}
export default Profile;